<template>
  <div>
    <el-row>
      <el-col :span="6"><div class="grid-content bg-purple" @click="click">1</div></el-col>
      <el-col :span="6"><div class="grid-content bg-purple-light" @click="click">2</div></el-col>
      <el-col :span="6"><div class="grid-content bg-purple"></div></el-col>
      <el-col :span="6"><div class="grid-content bg-purple-light"></div></el-col>
    </el-row>
  </div>
</template>

<script>
  import { mapState } from 'vuex'

  export default {
    name: 'app',
    data () {
      return {
      }
    },
    computed: {

    },
    methods: {
        click(){
          this.$router.push('/layout02')
        }
    }
  }
</script>

<style>
  .el-row {
    margin-bottom: 20px;
  &:last-child {
     margin-bottom: 0;
   }
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>



